:root {
	--background: #1f1b24;
	--primary: #ffe4c4;
}

body {
	background-color: var(--background);
	color: aliceblue;
	user-select: none;
	font-family: Roboto, Raleway, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	width: 100%;
}

.App {
	text-align: center;
}

#board {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 200px;
	width: 80%;
	margin: 48px auto;
	line-height: 108px;
	font-size: 80px;
	background-color: var(--primary);
	border-radius: 8px;
	text-transform: uppercase;
	color: #1f1b24;
	font-weight: 800;
}

#inputbox {
	font-size: 20px;
	margin: 0 10px;
	padding: 0;
	height: 48px;
	width: 70%;
	box-sizing: border-box;
	text-transform: uppercase;
	border: none;
	outline: none;
	padding: 8px 16px;
}

#btnreset {
	background-color: var(--primary);
	height: 48px;
	width: 72px;
    font-weight: 500;
	font-size: 20px;
	padding: 0px !important;
	margin: 0px;
	outline: none;
	border: none;
	cursor: pointer;
}

#timetaken {
	margin: 24px;
	font-size: 32px;
}


#credit {
	text-align: center;
	display: block;
	position: absolute;
	bottom: 0px;
	width: 100%;
}

#credit a {
	color: var(--primary);
}

/** Code to remove uppercase property from inputbox placeholder */
::-webkit-input-placeholder { /* WebKit browsers */
    text-transform: none;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    text-transform: none;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
    text-transform: none;
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
    text-transform: none;
}
::placeholder { /* Recent browsers */
    text-transform: none;
}